import { createAction,handleActions } from 'redux-actions';
import {combineReducers} from 'redux';
import * as Actions from "../actions/virusActions"




const simulationState = {

    rawData:[],
    primaryCountry:null,
    secondaryCountry:null,

    countrySelectedList:[{value:null,label:null}],


    graphType:'totalGraph',
    primaryCountryDataSet:null,
    secondaryCountryDataSet:null,



    onlyLastXDays:false,
    lastXDays:20,

    onlyFirstXDays:false,
    firstXDays:20,

    simulate:true,
    useRegressionTypeIndex:1,
    simulationAddDays:5,
    useModelFromOtherCountry:null,
    fromDayWithGreaterThan:1,

    usePredictionFromDates:false,
    predictionFromDates:null





};

export const simulationReducer = handleActions({




    [Actions.changeSimulation]: (state, action) => {

        const {name,value} = action.payload;


        let obj = {};
        obj[name]=value;

        //get data for primary



        return {
            ...state,
            ...obj


        }

    },
    [Actions.processRawData.SUCCESS]: (state, action) => {

        const {data} = action.payload;



        return {
            ...state,
            rawData:data
        }


    },
    [Actions.addCountrySelected]: (state, action) => {

        let newCountrySelected = [...state.countrySelectedList,{value:'',label:''}];

        return {
            ...state,
            countrySelectedList:newCountrySelected
        }


    },
    [Actions.removeCountrySelected]: (state, action) => {
        const {index} = action.payload;

        let newCountrySelected = [...state.countrySelectedList];
        newCountrySelected.splice(index,1);

        return {
            ...state,
            countrySelectedList:newCountrySelected
        }


    },
    [Actions.changeCountrySelected]: (state, action) => {
        const {index,value} = action.payload;

        let newCountrySelected = [...state.countrySelectedList];

        newCountrySelected[index] = {value:value,label:value};
        return {
            ...state,
            countrySelectedList:newCountrySelected
        }


    }



},simulationState);


